.main {    
    padding: 0px 167px 111px 262px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    /* row-gap: 104px; */
}

.main__title {
    width: 827px;
    color: #21201F;    
    font-family: YS Text;
    font-size: 56px;
    font-style: normal;
    font-weight: 900;
    line-height: 1; 
    letter-spacing: -1.12px;
    padding: 0;
    margin: 0;
    margin-bottom: 35px;
}

.main__title_narrow {    
    width: 407px;    
    font-size: 48px;
    font-style: normal;
    font-weight: 900;
    line-height: 1; 
    letter-spacing: -0.96px;
    margin: 0;
    margin-bottom: 35px;
}

.main__title_narrow_exclus {
    width: 827px;    
    margin-bottom: 90px;
}

.main__multicard-box {
    width: 86%;
    /* border: 1px solid green; */
    /* box-sizing: border-box;     */
    position: relative;
    display: flex;
    flex-direction: row;    
    justify-content: space-between;
    flex-wrap: wrap;
    row-gap: 55px;
    /* column-gap: 112px; */
    margin-bottom: 128px;
}

.main__multicard-box-element {
    /* border: 1px solid blue; */
    /* box-sizing: border-box; */
    max-width: 400px;
    max-height: 215px;
    display: flex;
    flex-direction: column;
    row-gap: 16px;    
}

.main__multicard-box-element__img {
    width: 83px;
    height: 83px;
    object-fit: scale-down;
}

.main__multicard-box-element__text {
    align-self: stretch;        
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 1.2;

}

.main__multicard-box-photos {
    width: 95%;
    max-height: 1201px;
    /* border: 1px solid green; */
    /* box-sizing: border-box;     */
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-wrap: wrap;    
    row-gap: 20px;    
    margin-bottom: 104px;
    transform: rotate(2deg);
}

.main__multicard-box-photo {
    width: 450px;
    height: 450px;
    border-radius: 64px;    
}

.main__multicard-box-photo-text {
    position: relative;
    top: -45px;
    right: 20px;
    width: 442px;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 1.2;
    margin: 0;
    margin-bottom: 15px;
    transform: rotate(-2deg);
}

.main__multicard-box-photo-mark {
    width: 510px;    
    position: absolute;
    top: 100px;
    left: -250px;
    object-fit: cover;
    transform: rotate(-2deg);
    z-index: 1;
}

.main__multicard-box-photo-strips {
    width: 360px;    
    position: absolute;
    top: -160px;
    right: -110px;
    object-fit: cover;
    transform: rotate(-2deg);
    z-index: 2;
}

.main__multicard-box-photo-cup {
    width: 570px;    
    position: absolute;
    bottom: -120px;
    right: -210px;
    object-fit: cover;
    transform: rotate(-2deg);
    z-index: 3;
}

.main__multicard-box-photo-text_last-box {
    width: 419px;
    position: relative;    
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 1.2;
    margin: 0;    
    /* margin-bottom: 69px; */
}

.main__scooter-round-text-box {
    /* border: 1px solid green;
    box-sizing: border-box; */
    position: relative;
    right: 60px;
    margin-top: 90px;
    margin-bottom: 0;
}

.main__scooter-plus-box {
    /* border: 1px solid green;
    box-sizing: border-box; */
    position: relative;
    left: 50px;
    margin-bottom: 125px;
}

.main__scooter-round-text-img {
    width: 405px;
    object-fit: cover;            
    margin-top: 40px;
}

.main__scooter-ellipse-round-text {
    width: 412.783px;
    height: 369.042px;
    transform: rotate(-140.03deg);
    flex-shrink: 0;
    border-radius: 412.783px;
    opacity: 0.29;
    background: #8241EF;
    filter: blur(100px);
    position: absolute;
    left: -40px;
    bottom: -40px;
    z-index: -90;
}

.main__scooter-ellipse-plus {
    width: 412.783px;
    height: 369.042px;
    transform: rotate(-140.03deg);
    flex-shrink: 0;
    border-radius: 412.783px;
    opacity: 0.29;
    background: #8241EF;
    filter: blur(100px);
    position: absolute;
    right: -30px;
    top: -30px;
    z-index: -91;
}

.main__scooter-plus-img {
    width: 412px;
    height: auto;
    object-fit: cover;    
}

.main__last-info-box {
    width: 90%;
    height: 1062px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    column-gap: 40px;
}

.main__hochu {
    width: 280px;
    margin-top: 40px;
}

