.header {
    /* border: 1px solid red; */
    /* width: 1440px; */
    max-height: 804px;
    position: relative;
    margin: auto;
    color: #21201F;
    /* leading-trim: both; */
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 1.3;
    padding: 125px 75px 125px 262px;
    
    /* border-bottom: 1px solid #545454; */
    /* box-sizing: border-box; */
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    /* align-items: center; */    
}

.header__back-color {
    width: 1440px;
    height: 804px;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0.22;
    background: var(--Linear, linear-gradient(0deg, rgba(255, 255, 255, 0.20) 0%, rgba(255, 255, 255, 0.20) 100%), linear-gradient(90deg, #FF5C4D 0%, #EB469F 25%, #8341EF 72%, #3F68F9 100%));
    z-index: -102;
}

.header__back-white-grad {
    width: 1440px;
    height: 804px;
    position: absolute;
    top: 0;
    left: 0;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.00) 0%, #FFF 100%);
    z-index: -101;
}

.header__logo {
    width: 176px;
}

.header__title {
    max-width: 420px;
    font-size: 72px;
    font-style: normal;
    font-weight: 900;
    line-height: 1;
    letter-spacing: -1.44px;
    margin: 0;
    margin-top: 60px;
    margin-bottom: 30px;
}

.header__text {
    max-width: 420px;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 1.3;  
    margin: 0;  
    margin-bottom: 35px;
}

.header__text_prices {
    /* width: 115px; */
    height: 33px;
    display: inline-flex;
    justify-content: center;
    align-items: center;

    /* flex-shrink: 0; */
    /* margin-left: -15px; */
    border-radius: 500px;
    background: linear-gradient(0deg, rgba(255, 255, 255, 0.63) 0%, rgba(255, 255, 255, 0.63) 100%), radial-gradient(141.42% 141.42% at 100% 0%, #E0B8FF 0%, #B6B9FF 100%);
}

.header__text_new-price {    
    /* z-index: -98; */
    display: inline-flex;
    width: 79px;
    /* padding-left: 8px; */
    /* padding-right: 8px; */
    margin-right: 5px;
    /* padding: 8px 12px; */
    justify-content: center;
    align-items: center;    
    
    border-radius: 500px;
    background: radial-gradient(141.42% 141.42% at 100% 0%, #E0B8FF 0%, #B6B9FF 100%);
}


.header__text_old-price {
    /* z-index: -99; */    
    padding-right: 18px;
    color: #808080;        
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 120%;
    /* text-decoration-style: solid; */
}

.header__image-scooter-community {
    width: 820px;
    /* height: 618px; */
    /* transform: rotate(-8.008deg); */
    position: absolute;
    margin-left: auto;
    top: 45px;
    right: 80px;
    z-index: -99;
}

.header_backgr-ellipse {
    width: 657.8px;
    height: 401.903px;
    transform: rotate(-140.03deg);
    flex-shrink: 0;
    border-radius: 657.8px;
    opacity: 0.5;
    background: #8241EF;
    filter: blur(100px);
    position: absolute;
    /* margin-left: auto; */
    top: 125px;
    right: 230px;
    z-index: -100;    
}

.header__batton {
    width: 318px;
    height: 65px;
    color: #21201F;
    text-align: center;    
    font-family: YS Text;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: .92;         
    margin: 0;
    padding: 0;    
    border-radius: 36px;
    background: #FFDE40;
    border: none;
    box-shadow: 0px 4px 98px 0px rgba(255, 222, 64, 0.59);
    cursor: pointer;
}

